import PropTypes from "prop-types"
import React from "react"

const ExperienceList = ({ children, name }) => (
	<div style={{
		paddingBottom: 20,
	}}>
		<h2>{name}</h2>
		{children}
	</div>
)

ExperienceList.propTypes = {
  name: PropTypes.string,
}

ExperienceList.defaultProps = {
  name: ``,
}

export default ExperienceList
